import { useState } from 'react';
import './nav.css';


const Nav = (props) => {

    const [toggle, setToggle] = useState(false);

    return (
        <nav>
            <div style={{ display: "flex", justifyContent: "center" }}>
                <div style={{ width: 900 }}>
                    <ul className="menu">
                        <li className="logo"><a href="/">El Búhu Tuerto</a></li>
                        <li className={`item ${toggle ? "active" : ""}`}><a href="#news" onClick={() => setToggle(false)}>News</a></li>
                        <li className={`item ${toggle ? "active" : ""}`}><a href="#restaurant" onClick={() => setToggle(false)}>Restaurant</a></li>
                        <li className={`item ${toggle ? "active" : ""}`}><a href="#about" onClick={() => setToggle(false)}>About</a></li>
                        <li className={`item button ${toggle ? "active" : ""}`}><a href="#about" onClick={() => setToggle(false)}>Menu</a></li>
                        <li className="toggle" onClick={(e) => {
                            setToggle(!toggle);
                        }}>
                            <span className="bars"></span>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>)
}

export default Nav;
